.addNewSection{
    padding: 20px 30px;
    background: #f2f2f2;
    text-align: center;
    cursor: pointer;
    svg{
        width: 30px;
    }
}
.allSectionTypeWrp{
    // display: flex;
}
.each_section_Wrp{
    // width: 25%;
    // -ms-flex: 0 0 25%;
    // flex: 0 0 25%;
}
.each_section_Wrp{
    text-align: center;
    border: 1px solid #eee;
    padding: 20px;
    cursor: pointer;
    border-radius: 20px;
    -webkit-transition: all ease-in-out 300ms;
    transition: all ease-in-out 300ms;
    .aicon{
        width: 80px;
        height: 80px;
        // background: #f2f2f2;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        svg{
            width: 40px;
            height: 40px;
            color: #c3c3c3;
        }

    }
    .section_title{
        font-size: 18px;
        font-weight: 500;
    }
   
}

.each_section_Wrp:hover, .each_section_Wrp.active{
    transform: scale(1.1);
    .aicon{
        svg{
            color: #fff;
        }
    }
    .section_title{
        color: #fff; 
    }
    background: #9B4DFF;    
}